/* eslint-disable react/no-array-index-key */
/* eslint-disable react/button-has-type */
import { useState } from "react";
import { motion } from "framer-motion";
import './slider.scss';

const images = [
  "https://i.natgeofe.com/n/548467d8-c5f1-4551-9f58-6817a8d2c45e/NationalGeographic_2572187_3x4.jpg",
  "https://cdn.britannica.com/91/181391-050-1DA18304/cat-toes-paw-number-paws-tiger-tabby.jpg",
  "https://upload.wikimedia.org/wikipedia/commons/4/4d/Cat_November_2010-1a.jpg",
  "https://static01.nyt.com/images/2022/11/29/science/00tb-cats1/00tb-cats1-mediumSquareAt3X.jpg",
  "https://i.natgeofe.com/n/548467d8-c5f1-4551-9f58-6817a8d2c45e/NationalGeographic_2572187_3x4.jpg",
  "https://cdn.britannica.com/91/181391-050-1DA18304/cat-toes-paw-number-paws-tiger-tabby.jpg",
  "https://upload.wikimedia.org/wikipedia/commons/4/4d/Cat_November_2010-1a.jpg",
  "https://static01.nyt.com/images/2022/11/29/science/00tb-cats1/00tb-cats1-mediumSquareAt3X.jpg",
];

const Slider = () => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const handlePrevClick = () => {
    setCurrentIndex((currentIndex - 2 + images.length) % images.length);
  };

  const handleNextClick = () => {
    setCurrentIndex((currentIndex + 2) % images.length);
  };

  return (
    <div className="slider">
      <div className="slider-container">
        <motion.div
          className="slider-track"
          style={{ x: -currentIndex * 300 }}
          transition={{ ease: "easeInOut", duration: 2000 }}
        >
          {images.map((image, index) => (
            <div className="slider-item" key={index}>
              <img src={image} alt={`Slide ${index}`} />
            </div>
          ))}
        </motion.div>
      </div>
      <div className="slider-controls">
        <button onClick={handlePrevClick}>Prev</button>
        <button onClick={handleNextClick}>Next</button>
      </div>
    </div>
  );
};

export default Slider;